.App {
  text-align: center;
  height: 100vh;
}

#wordle-area{
  position: relative;
  z-index: -1;
}

ul{
  list-style: none;
  padding-left:0 !important;
}
li{
  letter-spacing: 50px;
  font-size: 30px;
}

form button{
  margin:30px;
}

.wordle-letter{
  margin:10px;
  font-size: 30px;
  border:solid 2px #000;
  width:50px;
  height:35px;
  padding:5px 0 10px 0;
  display: inline-block;
  animation: pop 1s forwards;
  cursor: default;
  text-transform: uppercase;
  transform: scaleY(0);
}

.yellow{
  background:rgb(227, 227, 115);
}

.green{
  background: rgb(92, 187, 92);
}

.gray{
  background:rgb(206, 199, 199);
}

#menus{
  position: absolute;
  right:5%;
  top:5%;
}

#menus .menu-btn{
  background:rgba(62, 62, 62, 0.371);
  border-radius: 20px;
  width:60px;
  height:60px;
  cursor: pointer;
}


#menus p{
  font-size: 40px;
  color:#fff;
  padding-top:2px;
  margin:20px;
}

#empty-grid{
  position: absolute;
  width:100%;
  top:0;
  pointer-events: none;
  z-index: -1;
}

#empty-grid .wordle-letter{
  border:2px solid rgba(62, 62, 62, 0.371);
  color:transparent;
}

@keyframes pop {
    from {transform: scaleY(-1);}
    to {transform: scaleY(1);}
}

@keyframes slide {
  0%{top:-150px}
  30%{top:100px}
  70%{top:100px}
  100%{top:-150px}
}

#empty-grid .wordle-letter{
  animation: none !important;
  transform: scaleY(1);
}

#error-popup{
  position: absolute;
  right: 25%;
  top:-150px;
  width:150px;
  background:#fff;
  border:solid 5px red;
  border-radius: 10px;
}

.popup-slide{
  animation: slide 4s forwards;
}

@media only screen and (max-width: 1000px) {
  .modal-area .modal-box{
    width:100%;
    left:0;
  }
}

@media only screen and (max-width: 768px) {
  .wordle-letter{
    width:35px;
    height:15px;
    margin: 5px;
    font-size: 20px;
    padding:5px 0 15px 0;
  }

  .modal-area .modal-box p{
    font-size: 14px;
  }
}