.modal-area{
    width:100%;
    height:100%;
    position: absolute;
    top:0;
    left:0;
    background:rgba(63, 63, 63, 0.289);
    display: none;
}

.modal-area .modal-box{
    background:#fff;
    position: relative;
    top:25%;
    left:25%;
    width:50%;
    min-height: 50%;
}

.modal-area .modal-box h1{
    padding:10% 0 2% 0
}

.modal-area .modal-box p{
    font-size: 25px;
}

.modal-area .modal-box span{
    margin: 0 10px;
}

.modal-area .modal-box button{
    margin: 40px 30px;
    padding:10px 30px;
}

.modal-show{
    display: block !important;
    animation: fadeIn 0.6s linear;
}

.modal-animation{
    animation: slideRight 1s linear;
}

#win-modal-box div{
    position: absolute;
    right:50px;
    top:20px;
    font-size: 30px;
    color: rgba(62, 62, 62, 0.5);
    cursor:pointer;
}

@keyframes fadeIn {
    from {opacity:0}
    to {opacity:1}
}

@keyframes slideRight {
    0% {top:-50%}
    100% {top:25%}
}